.App {
  text-align: center;
}

.Earth-image {
  animation: Earth-image-spin infinite 60s linear, around infinite 60s linear;
  height: 8vw;
  cursor: pointer;
  position: absolute;
  top: 0;
}

.App-header {
  background-color: #18191b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes Earth-image-spin {
  from {
    transform: rotate(1440deg);
  }
  to {
    transform: rotate(0deg);
  }
}

@keyframes Earth-image-shrink {
  from {
    left: -8vw;
  }
  to {
    left: 100%;
  }
}


@keyframes around {
  0% {
    top: 0vh;
    left: 0vw;
  }
  25% {
    top: 0vh;
    left: 92vw;
  }
  50% {
    top: calc(100vh - 8vw);
    left: 92vw;
  }
  75% {
    top: calc(100vh - 8vw);
    left: 0vw;
  }
  100% {
    top: 0vh;
    left: 0vw;
  }
}